
<template>
   <section>
      <loader :is-visible="isLoading"></loader>
      <tg-list :IsShowAction="false"  :showCheckBox="false"  :listType="['List']" ModuleName="ExpenseApproval" SubModuleCode="ExpenseApproval"
            IdentityColumn="EXPENSEID" HeaderText="Expense" :ListData="ExpenseApprovalData" :HeaderData="Headers"            
            :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
            :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{data}">
                <template v-if="data.column.COLUMN_NAME == 'Status'">                
                    <a class="inactive-disabled" >
                        <span v-bind:class="getStatusClass(data.row.Status, 'Status')" v-if="data.row.Status !='SentForCorrection'">
                            {{data.row[data.column.COLUMN_NAME]}}
                        </span>
                        <span v-bind:class="getStatusClass(data.row.Status, 'Status')" v-if="data.row.Status =='SentForCorrection'">
                            {{ $t('SentForCorrection') }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Amount'">                                  
                        {{convertDecimalValueInFormat(data.row.Amount) +' '+data.row.ISO_CODE}}                   
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Comment'"> 
                    <a href="javascript:void(0);"  title="" class="viewcomment commenticon enable ml-3" @click="openAddNewEvent(data.row.EXPENSEID)">
                        <i class="fa fa-comment-o text-dark action-icon"></i>
                        <span class="noti-circle noti-blue text-center">{{ data.row.COMMENTSCOUNT }}</span>
                    </a>
                </template>
            </template> 
         </tg-list> 
         <ExpenseComments v-if="showCommentModal" @CloseExpenseComments="CloseExpenseComments()" v-bind:expenseid="expid" :CommentHeader="CommentHeader" :isSingleCommentforMultipleExpenses="isSingleCommentforMultipleExpenses" :statuscode="statuscode" @fetchdata="fetchdata()"></ExpenseComments>
        </section>
  </template>
  <script>
    import ExpenseDataServices from "@/services/DataService";
    import Schema from '../ExpenseHistory/HistorySchema'
    import ExpenseComments from '../../../components/Common/Modal/ExpenseComments.vue';
 export default { 
    components:{ExpenseComments},
    data(){
        return{            
            Headers:[
                {
                        "COLUMN_NAME": "Date",
                        "DATA_TYPE": "date",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Date",
                        "DISPLAY_ORDER": 1
                    },
                    {
                        "COLUMN_NAME": "Title",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Title",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "UserName",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "UserName",
                        "DISPLAY_ORDER": 3
                    },
                    {
                        "COLUMN_NAME": "Category",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Category",
                        "DISPLAY_ORDER": 4
                    }, 
                    {
                        "COLUMN_NAME": "TRIP",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Trip",
                        "DISPLAY_ORDER": 5
                    },                    
                    {
                        "COLUMN_NAME": "Project",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Project",
                        "DISPLAY_ORDER": 6
                    },
                    {
                        "COLUMN_NAME": "Amount",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Amount",
                        "DISPLAY_ORDER": 7,
                        settings:{
                            isInSlot : true
                        }
                    },                   
                    {
                        "COLUMN_NAME": "Status",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Status",
                        "DISPLAY_ORDER": 8,
                        settings:{
                            isInSlot : true
                        }
                    },
                    {
                        "COLUMN_NAME": "Comment",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Comment",
                        "DISPLAY_ORDER": 9,
                        settings:{
                            isInSlot : true,
                            clickEvent: this.clickEventOfColumn
                        }
                    }                       
            ],
            widgets:[],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            searchCondition: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',            
            noRecord:false,
            CurrentPage: 1,
            TotalPages: 0,
            ExpenseApprovalData:null,
            CommentHeader:null, 
            isSingleCommentforMultipleExpenses:null,         
            showCommentModal:null,  
            statuscode :null,  
            expid:null,
            LegendArray:[],
            isLoading:false,
            View_List:[],
            UserType:''
        }
    },
    created:function(){
        var vm=this;
        vm.FetchData();
        Schema.bindleftCommonSearchdropdown(vm,"Project","no","");
        Schema.bindleftCommonSearchdropdown(vm,"User","","EXPENSEAPPROVAL");
        vm.UserType = vm.GetUserInfo.UserType;
        vm.LegendArray = [
            {
                class: 'legend-pending colorboxsquare',
                text: (vm.$t('Overridable', ['History']))
            },
            {
                class: 'legends_medium colorboxsquare',
                text: (vm.$t('Forcible', ['History']))
            }
        ]
    },
    methods:{  
        actionButtonClick: function (callbackfunction, item) {
        },
        renderActions(row) {
        },
        getStatusClass: function (status, columnName) {
                var returnClass = ''
                if (columnName=='Status') {
                    switch (status) {
                        case 'Active': case 'Approved':
                            returnClass = 'px-2 bg-success text-center text-white d-block';
                            break;
                        case 'Inactive': case 'Closed': case 'Cancelled': case 'Rejected':
                            returnClass = 'px-2 bg-danger text-center text-white d-block';
                            break;

                        case 'Approval Pending': case 'Partially Approved':
                            returnClass = 'px-2 bg-info text-center text-white d-block';
                            break;
                        case 'Received': case 'Partially Received':
                            returnClass = 'px-2 bg-secondary text-white text-center d-block';
                            break;
                        case 'Draft':
                            returnClass = 'px-2 bg-warning text-center text-white d-block';
                            break;
                        case 'No Gin':
                            returnClass = 'px-2 bg-primary text-center text-white d-block';
                            break;
                        default:
                          
                            returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    }
                    return returnClass;
                }
            }, 
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {  
                case 'backtolist':
                    this.GoBack();
                    break;
            }
        },
        GoBack:function(){            
            this.$router.push({ name: 'ExpenseApproval' });
        },
        openAddNewEvent:function(ths){            
            this.CommentHeader='Comment'; 
            this.isSingleCommentforMultipleExpenses=0;          
            this.showCommentModal=true;
            this.statuscode = 'History';
            this.expid = ths;        
        },
        CloseExpenseComments:function(){
           this.showCommentModal=false; 
        },
        leftCommonSearch: function (value) {                                                        
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
              this.searchCondition = value.searchConditionJson;
              this.searchCondition =  this.searchCondition.replace("|", "&dateTo=");
            }
            else {
              this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {            
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },      
        FetchData:function(){ 
            var vueObj = this;
            vueObj.isLoading = true;     
            var params = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&${vueObj.searchCondition}`               
            ExpenseDataServices.ExpenseApprovalHistory(params).then(function(response){
                vueObj.isLoading = true;                 
                if(response.data != null){
                    if(response.data.length>0){     
                        vueObj.ExpenseApprovalData = response.data;
                        vueObj.ExpenseApprovalData.forEach((row) => {
                            row.additionalClass='';
                            if(row.ISFORCIABLE == 1){
                                row.additionalClass = 'high-bar';
                            }
                            if(row.ISOVERRIDABLE==1 || row.STATUSCODE=='EXP_APPROVED' || row.STATUSCODE == 'EXP_REJECTED'){
                                row.additionalClass = 'medium-bar';
                            }
                        })                      
                        vueObj.TotalRecords=response.data[0].TotalRecords;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.isLoading = false;
                    }
                    else{
                        vueObj.noRecord = true;
                        vueObj.ExpenseApprovalData = [];
                        vueObj.TotalRecords = 0
                    }
                    vueObj.isLoading = false;
                }
                else{
                    vueObj.noRecord = true;
                    vueObj.ExpenseApprovalData = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function () { vueObj.CheckBoxBootstrap() }, 100);
                setTimeout(function () {vueObj.ResponsiveDataTable('tablelistingdata')}, 500);  
                vueObj.isLoading = false;
            });
        }
    }
}
</script>