<template>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{this.CommentHeader}}
                            </h5>
                            <button v-on:click="CloseExpenseComments()" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div class="modal-body">
                            <Form ref="CommentForm" v-slot="{ valid,errors}">
                                <div v-if="visibility==1" :class="{'d-none':showModelClientCompany==true}">
                                   <div class="row">
                                        <div class="col-lg-12">
                                        <div class="form-group">
                                           <label>{{$t('Title')}}:<span class="text-danger">*</span></label>
                                           <Field name="Title" :rules="{'required':true}" label="Title" v-slot="{errors}">
                                               <input  name="Title" id="Title" type="text" maxlength="100" v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" 
                                               v-model="CommentTitle" />
                                               <span v-if="errors[0]" name="Name" class="invalid-feedback d-block">{{errors[0]}}</span>
                                           </Field>
                                       </div> 
                                        <div class="form-group">
                                           <label>{{$t('Description')}}:<span class="text-danger">*</span></label>
                                           <Field name="Description" :rules="{'required':true}" label="Description" v-slot="{errors}">
                                               <textarea style="max-height:300px!important;min-height:100px!important" maxlength="250" id="description" 
                                               name="Description" v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" v-model="Commentdesc"  />
                                               <span v-if="errors[0]" name="Name" class="invalid-feedback d-block">{{errors[0]}}</span>
                                           </Field>
                                       </div> 
                                       <div class="form-group" v-if="access==1">
                                            <label>Visibility:<span class="text-danger"></span></label>
                                            <div class="form-control pl-0 border-0">
                                                <Field name="ReimburseBy" :rules="{'required':true}" label="Reimburse By" v-slot="{errors}">
                                                    <div class="form-check form-check-inline">                                                                                                                   
                                                        <input  type="radio" id="Public" value="Public" name="Public" v-model="SelectedVale" />                                                        
                                                        <label class="form-check-label font-weight-normal ml-2" for="Client">{{$t('Public')}}</label>                                                            
                                                    </div>                                                        
                                                    <div class="form-check form-check-inline">                                                         
                                                        <input  type="radio" id="Private" value="Private" v-model="SelectedVale" name="Company"/>
                                                        <label class="form-check-label font-weight-normal ml-2" for="Company">{{$t('Private')}}</label>
                                                    </div>                                                    
                                                    <span v-if="errors[0]" name="Name" ref="Visibility" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                </Field>
                                            </div>                                                    
                                        </div>
                                        </div> 
                                        <div class="col-lg-12">
                                       <div class="row flex-row-reverse">
                                           <div class="col-lg-6 text-right" v-if="isSingleCommentforMultipleExpenses==1">                   
                                               <a class="btn btn-danger mr-2" v-on:click="Skip(valid,errors)" title="Skip"  href="javascript:;"><span>{{$t('Skip')}}</span><em class="fa fa-forward pl-2"></em></a>
                                               <a class="btn btn-success" v-on:click="APostNewComment(valid,errors)"  :title="$t('PostNewComment')" href="javascript:;"><em class="fa fa-send pr-2"></em><span>{{$t('PostNewComment')}}</span></a>
                                           </div>
                                           <div class="col-lg-6 text-right" v-if="isSingleCommentforMultipleExpenses==0">                   
                                               <a class="btn btn-success" v-on:click="PostNewComment(valid,errors)" id="postcomment" :title="$t('PostNewComment')" href="javascript:;"><em class="fa fa-send pr-2"></em>{{$t('PostNewComment')}}</a>                       
                                           </div>
                                           <div class="col-lg-6">
                                               <div class="py-2"><small class="text-danger mandatory">{{$t('MandatoryString')}}</small></div>
                                           </div>
                                       </div>                                      
                                        </div>                                                                                                
                                   </div>                                
                                </div>
                                <div v-if="isSingleCommentforMultipleExpenses==1">                                
                                <div class="col-lg-12">                                    
                                    <div class="row" v-if="showModelClientCompany">
                                        <div class="w-100 col-12">
                                            <div class="alert alert-warning mb-3">
                                                {{$t('ExpenseEntryWillBeReimbursedBy')}}
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                <div class="form-group">
                                                    <label>{{$t('ReimburseBy')}}:<span class="text-danger">*</span></label>
                                                    <div class="form-control pl-0 border-0">
                                                        <Field name="ReimburseBy" :rules="{'required':true}" label="Reimburse By" v-slot="{errors}">
                                                            <div class="form-check form-check-inline">                                                                                                                   
                                                                <input type="radio" id="Client" value="Client" name="ReimburseBy" v-model="SelectedVale" />                                                        
                                                                <label class="form-check-label font-weight-normal ml-2" for="Client">{{$t('Client')}}</label>                                                            
                                                            </div>                                                        
                                                            <div class="form-check form-check-inline">                                                         
                                                                <input type="radio" id="Company" value="Company" v-model="SelectedVale" name="ReimburseBy"/>
                                                                <label class="form-check-label font-weight-normal ml-2" for="Company">{{$t('Company')}}</label>
                                                            </div>
                                                            <span v-if="errors[0]" name="Name" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                        </Field>
                                                    </div>                                                    
                                                </div>
                                            </div>
                                            <div class="col-lg-12 text-right p-0">                           
                                              <div class="search-btm-btn">
                                                    <a class="btn btn-success" v-on:click="SubmitExpenseApproval(valid,errors,'Skip')" title="submit" href="javascript:;"><i class="fa fa-save pr-2"></i>{{$t('Submit')}}</a>
                                              </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>                               
                            </div>
                            </Form>                            
                            <div class="row" >
                                <div class="col-lg-12">
                                     <div class="listing" v-if="isSingleCommentforMultipleExpenses==0">
                                        <div id="Expenseaccordion" class="w-100 activity-tab" >
                                            <div class="card" v-for="(item, parentIndex) in CommentExpenseData" :key="parentIndex">
                                                <div class="card-header">
                                                    <a class="card-link collapsed" data-toggle="collapse" v-bind:href="'#Expensecollapse'+parentIndex">
                                                        <div class="conversation-date heading-border">
                                                            <span><em class="pr-1" aria-hidden="true"></em>{{item.groupKey}}</span>
                                                        </div>
                                                    </a>
                                                </div>
                                                <div v-bind:id="'Expensecollapse'+parentIndex" v-bind:class="{'collapse':true, 'show':parentIndex==0}">
                                                 <div class="card-body" v-for="(childItem, childIndex) in item.groupData" :key="childIndex">
                                                     <div class="conversation-main"  v-if="childItem.EXPENSE_ACCESS==1">                                                            
                                                         <div class="conversation-time"><span>{{$options.filters.formatTime(childItem.CREATED_AT)}}</span></div>
                                                         <div class="conversation-box-con">
                                                             <div class="conversation-box">
                                                                 <div class="name-circle light-yellow">                                                                       
                                                                     <img class="img-circle avatar" :src="blobUrl" width="51" height="51" alt="Image" />
                                                                 </div>
                                                                 <div class="conversation-header addednote-ticket">
                                                                     <span>
                                                                         <span class="sender-name">{{childItem.EXPENSE_COMMENT_TITLE}}</span>
                                                                         <span class="sender-department" :title="item.EXPENSE_COMMENT_DESC"> ({{childItem.EXPENSE_COMMENT_DESC}})</span>
                                                                     </span>
                                                                 </div>
                                                                 <div class="conversation-content">
                                                                     <div class="col-12 p-0 notesdata">
                                                                         <h6 class="text-muted time"><i><b>{{childItem.CREATED_BY}}</b>, {{$t('PostedOn')}}: <span class="text-danger" >{{$options.filters.formatDateTime(childItem.CREATED_AT)}}</span> </i></h6>
                                                                     </div>
                                                                 </div>
                                                             </div>
                                                         </div>
                                                     </div>                                                          
                                                     <div  class="conversation-main" v-if="childItem.EXPENSE_ACCESS == 0">
                                                         <div class="conversation-time"><span>{{childItem.CREATED_AT}}</span></div>
                                                         <div class="conversation-box-con">
                                                             <div class="conversation-box">
                                                                 <div class="name-circle light-yellow">                                                                        
                                                                      <img class="img-circle avatar" :src="blobUrl" width="51" height="51" alt="Image" />  
                                                                 </div>
                                                                 <div class="conversation-header addednote-ticket">
                                                                     <span>
                                                                         <span class="sender-department"> {{childItem.EXPENSE_COMMENT_DESC}}</span>
                                                                     </span>
                                                                 </div>
                                                                 <div class="conversation-content">
                                                                     <div class="col-12 p-0 notesdata">
                                                                         <h6 class="text-muted time"><i><b>{{childItem.CREATED_BY}}</b>, {{$t('PostedOn')}}: <span class="text-danger" >{{childItem.CREATED_AT}}</span> </i></h6>
                                                                     </div>
                                                                 </div>
                                                             </div>
                                                         </div>
                                                     </div>                                                        
                                                 </div>
                                                </div>
                                            </div>                                            
                                        </div>                                        
                                     </div>
                                     <div v-if="noRecordForTab && isSingleCommentforMultipleExpenses==0"  class="text-center text-danger border p-3 mt-4">{{$t('NoRecordfound')}}</div>
                                </div>                                
                            </div> 
                        </div>
                    </div>
                </div>
            </div>
        </div>

</template>
<script>
import ExpenseDataServices from "@/services/DataService";
export default {  
    props: {
        expenseid: {
            type: String,
            required: true
        },
        CommentHeader:{
           type: String,
            required: true 
        },
        isSingleCommentforMultipleExpenses:{
            type: Number,
            required: true
        },       
        statuscode:{
            type:String,
            required:true
        },
        visibility:{
            type:Number,
            required:true
        },
        access:{
            type:Number,
            required:true
        }
    },  
    data: function (){
        return{
            isLoading:false,
            CommentTitle:'',
            Commentdesc:'',
            Expense_Id:'',
            userid: '',
            usertype:'',
            CommentExpenseData:[],
            showModelClientCompany:false,
            SelectedVale:'',
            objectPostString:'',
            noRecordForTab:true,
           // CommentListing:true,
            SkipParam:false,
            containerName:null,
            avatar:'',
            blobUrl:''
        }
    },
    created: function(){
        this.userid = this.GetUserInfo.ID;
        this.usertype = this.GetUserInfo.UserType; 
        this.containerName=this.GetUserInfo.ContainerName;                  
        this.CommentList();
        this.GetBlobUrl();
    },
    updated: function(){                 
        this.CommentList();
        this.GetBlobUrl();
    },
    methods:{
        GetBlobUrl: function () {
            ExpenseDataServices.GetBlobUrl().then((response) => {                
                var url = response.data[0].CONFIG_DATA_VALUE;
                if(this.avatar==null){
                    this.blobUrl = '/Content/images/DefaultImages/uc_user_new.jpg';
                }
                else{
                    this.blobUrl = url + '/' + this.containerName + '/' + 'Upload' + '/' + 'UserProfileImage' + '/' + this.avatar
                } 
        })
        },
        Skip:function(){
            this.showModelClientCompany=true;  
            this.CommentTitle='',
            this.Commentdesc='' 
            this.SkipParam=true;  
        },
        CloseExpenseComments:function(){
            this.$emit('CloseExpenseComments');
        },
        PostNewComment:function(valid,errors){     
            var vueObj=this;
            vueObj.$refs.CommentForm.validate().then((result) => {            
                if (result) { 
                    var jsoObj={
                        ExpenseCommentId:0,
                        CommentTitle:vueObj.CommentTitle,
                        Commentdesc:vueObj.Commentdesc,
                        Expense_Id:vueObj.expenseid,
                        EXPENSE_ACCESS:1,
                        StatusId:1001
                    };
            var objectPostString = JSON.stringify(jsoObj);
            ExpenseDataServices.SaveComment(objectPostString).then(function(response){
                if(response.status==200){
                    vueObj.ShowAlert(vueObj.$t('AddedSuccess',[vueObj.$t('Comments')]), "success", true,  vueObj.$t("Alert")); 
                    vueObj.noRecordForTab = false;
                    vueObj.CommentTitle="";
                    vueObj.Commentdesc="";
                    vueObj.$refs.CommentForm.reset();
                    vueObj.CommentList();
                } 
            });
         }});            
        },        
        APostNewComment:function(valid,errors){
          //  debugger
            var vueObj=this;            
            vueObj.$refs.CommentForm.validate().then((result) => { 
                if(vueObj.$refs.CommentForm.errors.ReimburseBy.length>0){
                  //  debugger
                    vueObj.$refs.Visibility.outerHTML='';
                    result=true;                    
                }
                if(vueObj.$refs.CommentForm.errors.Description.length>0){
                    result=false;
                }
                if(vueObj.$refs.CommentForm.errors.Title.length>0){
                    result=false;
                }                
                if (result) {
                if(vueObj.statuscode=="EXP_SEND_FOR_CORRECTION")
                {
                    vueObj.showModelClientCompany=false;  
                    vueObj.noRecordForTab = false;                 
                    vueObj.SubmitExpenseApproval();                              
                }else{
                   // debugger
                    if(vueObj.SkipParam==false)
                    {
                        vueObj.showModelClientCompany=false;
                        vueObj.SubmitExpenseApproval(); 
                    }  
                }
            }else{
                console.log(vueObj.$refs.CommentForm.errors);
            }
         }); 
        },
        CommentList:function(){
           var vueObj=this;
           vueObj.isLoading=true;
           var url="id="+vueObj.expenseid;          
           ExpenseDataServices.CommentsList(url).then(function(response){
                if(response.data!=null)
                {
                    vueObj.CommentExpenseData = [];
                    var fullJSON = response.data;
                    var gruopBy = "";
                    $.each(fullJSON,$.proxy(function(index,item){                        
                        gruopBy=vueObj.$options.filters.formatDate(item.CREATED_AT);
                        var chkGroup = $.grep(vueObj.CommentExpenseData, function (element, index) {                            
                            return element.groupKey == gruopBy;
                            }
                        );
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(fullJSON,function (element, index) {                                
                                return vueObj.$options.filters.formatDate(element.CREATED_AT) == gruopBy;
                              }
                         );
                        if (returnedData.length > 0) {
                            var objGroup = {
                              groupKey: gruopBy,
                              groupData: returnedData,
                            };
                            vueObj.CommentExpenseData.push(objGroup);                           
                            vueObj.avatar=vueObj.CommentExpenseData[0].groupData[0].avatar;
                            vueObj.noRecordForTab = false;
                        }
                    }
                    },this));                   
                }
                else{
                    vueObj.noRecordForTab = true;
                    vueObj.ExpenseData=[];
                } 
                vueObj.ExpenseData=[]; 
                 vueObj.isLoading=false;           
            }); 
        },     
        SubmitExpenseApproval:function(valid,errors,Skip){
            debugger
            var vueObj=this;       
            vueObj.$refs.CommentForm.validate().then((result) => { 
                if(Skip=='Skip'){               
                    if(vueObj.$refs.CommentForm.errors.Description.length>0){
                        result=true;
                    }
                    if(vueObj.$refs.CommentForm.errors.Title.length>0){
                        result=true;
                    }  
                    if(vueObj.$refs.CommentForm.errors.ReimburseBy.length>0){
                        result=false;
                    }
                }else{
                    result=true;
                }
            if (result) {  
                //debugger
                var msg='';
                if(vueObj.statuscode=='EXP_APPROVED'){
                    msg=vueObj.$t('ExpenseApprove');
                }
                if(vueObj.statuscode=='EXP_REJECTED'){                    
                    msg=vueObj.$t('ExpenseReject');
                }
                if(vueObj.statuscode=='EXP_SEND_FOR_CORRECTION'){
                    msg=vueObj.$t('ExpenseApproveSendForCorrection');
                }
                vueObj.confirmR(msg, true, (vueObj.$t("Delete") + "  " + vueObj.$t("ExpenseApproval")), function () {               
                    var jsoObj=`ids=${vueObj.expenseid}&statusCode=${vueObj.statuscode}&approvalTypeId=2&CommentTitle=${vueObj.CommentTitle}&Commentdesc=${vueObj.Commentdesc}&commentAccess=1&isForciable=0&isOverridable=0&reimburseBy=${vueObj.SelectedVale}`                    
                    ExpenseDataServices.AddApproverStatusListing(jsoObj).then(function(response){ 
                        debugger
                        if(response.status==200){                            
                            if(vueObj.statuscode=='EXP_APPROVED')
                            {
                                vueObj.$emit('CloseExpenseComments');                                
                                vueObj.$emit('fetchdata');
                                $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                $("[id^='SendForReject']").removeClass('enable').addClass('disabled');
                                $("[id^='SendForApprove']").removeClass('enable').addClass('disabled'); 
                                vueObj.ShowAlert(vueObj.$t('ExpenseApproveReject',[vueObj.$t('Approve')]), "success", true,  vueObj.$t("Alert"));
                            }
                            else if(vueObj.statuscode=='EXP_REJECTED')
                            {
                                vueObj.$emit('CloseExpenseComments');
                                vueObj.$emit('fetchdata');
                                $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                $("[id^='SendForReject']").removeClass('enable').addClass('disabled');
                                $("[id^='SendForApprove']").removeClass('enable').addClass('disabled'); 
                                vueObj.ShowAlert(vueObj.$t('ExpenseApproveReject',[vueObj.$t('Decline')]), "success", true,  vueObj.$t("Alert"));
                                
                            }
                            else{
                                vueObj.$emit('CloseExpenseComments');
                                vueObj.$emit('fetchdata');
                                vueObj.ShowAlert(vueObj.$t('ExpenseApproveSendForCorrection',[vueObj.$t('SendForCorrection')]), "success", true,  vueObj.$t("Alert"));
                            }                         
                        } 
                    });
             });
            }
         });            
        }
    }
}
</script>